.section-major {
  margin-bottom: 15px;
  padding: 10px 20px 10px 20px;
  border-left: 3px solid tomato;
  border-radius: 0 15px 15px 0;
  background-color: white;
}

.section-major__top {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: baseline;
}

.section-major__title > h1 {
  font-size: 26px;
}

.section-major__top img {
  width: 25px;
  height: 25px;
}

.section-major__top img:hover {
  cursor: pointer;
}

.section-done {
  border-left: 3px solid skyblue;
  color: gray;
}

.section-major__title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.section-major__content {
  font-size: 18px;
  margin-bottom: 15px;
  white-space: pre-wrap;
  line-height: 1.4;
}

.section-minorlist {
  margin-bottom: 5px;
}

.section-minor {
  font-size: 18px;
  margin-top: 10px;
}

.minor-done {
  text-decoration: line-through;
}
