.popup {
  width: 300px;
  height: 500px;

  border-radius: 15px;
  background-color: var(--black-bg-color);
  box-shadow: rgba(0, 0, 0, 0.7) 0px 5px 15px;

  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -170px;
  margin-top: -250px;

  padding: 20px;
  transition: height 0.5s ease-in-out, margin-top 0.5s ease-in-out;
}

.popup-container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.popup-container > div {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.popup-container__title {
  font-size: 24px;
  border-bottom: 1px solid;
  margin-bottom: 15px;
}

.popup-container__title,
.popup-container__content,
.popup-container__minorlist,
.popup-container__minoritem input,
.popup-container__minoradd {
  color: white;
  background-color: transparent;
}

.popup-container__title,
.popup-container__submit,
.popup-container__minoradd {
  font-family: "Jua", sans-serif;
}

.popup-container__content,
.popup-container__minoritem input {
  font-family: "Nanum Gothic", sans-serif;
}

.popup-container__content {
  padding: 5px;
  resize: none;
  font-size: 16px;
  margin-bottom: 15px;
  flex-grow: 1;
}

.popup-container__minorlist {
  margin-bottom: 15px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  border: 1px solid;
  height: 150px;
  transition: height 0.5s ease-in-out;
}

.popup-container__minorlist::-webkit-scrollbar {
  display: none;
}

.popup-container__minoritem {
  margin: 2px 3px 1px 3px;
  padding: 5px;
  border: 1px dotted;
  display: flex;
}

.popup-container__minoritem input {
  font-size: 14px;
  flex-grow: 1;
  margin-right: 5px;
}

.popup-container__minoradd {
  font-size: 16px;
  padding: 5px;
  width: 100%;
}

.popup-container__button {
  bottom: 0;
}

.popup-container__submit {
  font-size: 18px;
  padding: 10px;
  border: 1px solid;
  border-radius: 16px;
  vertical-align: middle;
  line-height: 15px;
  width: 45%;
}

.popup-container__submit,
.popup-container__minoradd {
  background-color: white;
  color: royalblue;
  cursor: pointer;
}

@media (max-height: 570px) {
  .popup {
    height: 350px;
    margin-top: -190px;
  }
  .popup.title-focused {
    margin-top: -50px;
  }

  .popup.content-focused {
    margin-top: -100px;
  }

  .popup.-focused {
    margin-top: -230px;
  }

  .popup-container__minorlist {
    height: 100px;
  }
}
