header {
  background-color: black;
  padding: 10px 20px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu-title h1 {
  color: beige;
  font-size: 24px;
  font-family: "Jua", sans-serif;
  margin: 0;
}

input[type="date"] {
  width: 145px;
  height: 24px;
  font-family: "Nanum Gothic", sans-serif;
  font-size: 16px;
  font-weight: 800;
  color: beige;
  border-radius: 5px;
  padding: 5px 0px 5px 20px;
  background-color: rgb(56, 56, 56);
}

input[type="date"]::-webkit-calendar-picker-indicator {
  margin-right: 10px;
  margin-top: 1px;
  font-size: 18px;
}

.gotoday {
  margin-left: 10px;
  font-size: 15px;
  padding: 7px;
  border-radius: 5px;
  background-color: beige;
  font-family: "Nanum Gothic", sans-serif;
}

.menu-button > img {
  width: 20px;
  height: 20px;
  background-color: beige;
}

@media (min-width: 768px) {
  .menu-button {
    display: none;
  }
}
