section {
  background-color: var(--white-bg-color);
  flex-grow: 1;
  padding: 30px;
  transition: margin 1s;
}

.consulting {
  overflow-y: scroll;
  height: 97%;
  padding-right: 10px;
}

.section-title {
  font-size: 32px;
}

.section-majorlist {
  margin-top: 20px;
}

@media (max-width: 768px) {
  section {
    margin-left: -250px;
  }
}
