@import "reset.css";
@import url("https://fonts.googleapis.com/css2?family=Archivo:wght@400;700&family=Jua&family=Nanum+Gothic:wght@400;800&display=swap");

body {
  margin: 0;
  font-family: "Nanum Gothic", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --white-bg-color: rgb(250, 250, 240);
  --black-bg-color: #282c34;
  --light-black: rgb(150, 150, 150);
}
